@import url("./reset.css");

.main__screen {
  display: flex;
}

.card__name {
  margin-top: 41px;
}

.card__list {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  gap: 45px;
}

.card {
  padding: 16px;
  display: flex;
  flex-direction: column;
}

.card__image {
  width: 191px;
  height: 232px;
  margin-bottom: 16px;
}

.card__content {
  width: 191px;
  display: flex;
  flex-direction: column;
}

.card__info {
  margin-bottom: 19px;
}

.card__model {
  margin-bottom: 8px;
  height: 48px;
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
  color: rgb(26, 31, 22);
}

.card__series {
  font-size: 16px;
  color: rgb(96, 105, 92);
}

.card__price {
  width: 175px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.card__price-number {
  font-size: 20px;
  line-height: 27px;
  color: rgb(26, 31, 22);
}

.addButton-bag {
  width: 38px;
  height: 36px;
  padding: 8px;
  border-radius: 9px;
  background: rgb(26, 31, 22);
  border: 1px solid rgb(26, 31, 22);
}

.vertical__line {
  height: 921px;
  margin-top: 51px;
}

.bag {
  padding: 41px 33px 0 33px;
  text-align: center;
}

.bag-list {
  width: 288px;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  gap: 16px;
  padding: 18px 30px;
}

.bag__button-img {
  text-align: -webkit-center;
}

.bag__img {
  width: 64px;
  height: 64px;
}

.bag__button {
  width: 163px;
  margin-bottom: 44px;
  padding: 8px 24px;
  display: flex;
  justify-content: space-between;
  font-family: Open Sans;
  font-size: 20px;
  line-height: 27px;
  border-radius: 14px;
  background: rgb(26, 31, 22);
  color: rgb(255, 255, 255);
}

.orders__list {
  width: 295px;
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.orders__list-text {
  font-size: 20px;
  line-height: 27px;
}
